	import api from '@/common/vmeitime-http/'
    export default {
		data(){
			return {
				array: ['请选择反馈类型', '商品相关', '功能异常', '优化建议', '其他'],
				index: 0,
				content: '',
				contentLength: 0,
				mobile: '',
				hasPicture: false,
				picUrls: [],
				files: []
			}
		},
		onShareAppMessage(res) {
		  return {
			title: '贝贝商城',
			imageUrl: '/static/temp/banner3.jpg',
			path: '/pages/index/index'
		  }
		},
		methods: {
			chooseImage: function(e) {
			    var that = this;
			    uni.chooseImage({
			      count: 1,
			      sizeType: ['original', 'compressed'],
			      sourceType: ['album', 'camera'],
			      success: function(res) {
				    that.files = that.files.concat(res.tempFilePaths)
			        that.upload(res);
			      }
			    })
			  },
			  upload: function(res) {
			    var that = this;
			    const uploadTask = uni.uploadFile({
			      url: 'http://mall.zhihuiyunqi.com/wx/storage/upload',
			      filePath: res.tempFilePaths[0],
			      name: 'file',
			      success: function(res) {
			        var _res = JSON.parse(res.data);
			        if (_res.errno === 0) {
			          var url = _res.data.url
			          that.picUrls.push(url)
					  that.hasPicture = true
			        }
			      },
			      fail: function(e) {
			        uni.showModal({
			          title: '错误',
			          content: '上传失败',
			          showCancel: false
			        })
			      },
			    })
			
			    uploadTask.onProgressUpdate((res) => {
			      console.log('上传进度', res.progress)
			      console.log('已经上传的数据长度', res.totalBytesSent)
			      console.log('预期需要上传的数据总长度', res.totalBytesExpectedToSend)
			    })
			
			  },
			  previewImage: function(e) {
			    uni.previewImage({
			      current: e.currentTarget.id, // 当前显示图片的http链接
			      urls: this.files // 需要预览的图片http链接列表
			    })
			  },
			  bindPickerChange: function(e) {
			      this.index = e.detail.value
			  },
			  mobileInput: function(e) {
			      this.mobile = e.detail.value
			  },
			  contentInput: function(e) {
			      this.contentLength = e.detail.cursor
			      this.content= e.detail.value
			  },
			  clearMobile: function(e) {
			      this.mobile = ''
			  },
			  submitFeedback: function(e) {
			    if (!uni.getStorageSync('haslogin')) {
			      uni.navigateTo({
			        url: "/pages/public/login"
			      });
			    }
			
			    let that = this;
			    if (that.index == 0) {
				  uni.showToast({title: '请选择反馈类型', icon: 'none'});
			      return false;
			    }
			
			    if (that.content == '') {
				  uni.showToast({title: '请输入反馈内容', icon: 'none'});
			      return false;
			    }
			
			    if (that.mobile == '' || !/^1[3|4|5|6|7|8|9][0-9]\d{8}$/.test(that.mobile)) {
				  uni.showToast({title: '请输入正确的手机号', icon: 'none'});
			      return false;
			    }
			
			    uni.showLoading({
			      title: '提交中...',
			      mask: true,
			      success: function() {}
			    });
				let url = '/feedback/submit'
			    api.httppost(url, {
			      mobile: that.mobile,
			      feedType: that.array[that.index],
			      content: that.content,
			      hasPicture: that.hasPicture,
			      picUrls: that.picUrls
			    }).then(function(res) {
			      uni.hideLoading();
			      if (res.data.errno === 0) {
			        uni.showToast({
			          title: '感谢您的反馈！',
			          icon: 'none',
			          complete: function() {
						  uni.navigateBack()
			          }
			        });
			      } else {
			        uni.showToast({title: res.data.errmsg, icon: 'none'});
			      }
			    });
			  }
        }  
    }  
